@import "~antd/dist/antd.css";

.h3 {
  font-family: fantasy;
  font-size: 40px;
  font-weight: bold;
  color: whitesmoke;
  margin-bottom: 2px;
}
.h1 {
  font-family: fantasy;
  font-size: 70px;
  font-weight: bold;
  color: whitesmoke;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  margin-top: 50px;
}
.h2 {
  font-family: fantasy;
  font-size: 35px;
  font-weight: bold italic;
  color: whitesmoke;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-left: 36%;
  align-items: center;
  align-content: center;
  align-self: center;
}
.h4 {
  font-family: fantasy;
  font-size: 10px;
  font-weight: italic;
  color: whitesmoke;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  margin-top: 5px;
}



.image {
  height: 48%;
  width: 48%;
  margin-left: 27%;
  border-radius: 50px;
  margin-top: 20px;
}
.border {
  border-style: solid;
  border-color: #0f5e9c;
  border-radius: 40px;
}

.grid-container {
  display: grid;
  grid-column-start: 1;
  grid-column-end: 3;
 
  grid-row-start: 1;
  grid-row-end: 14;
  grid-template-columns: 200px 200px;
  grid-template-rows: 200px 200px;
  background-color: rgba(14, 14, 27, 0.664);
  height: 95vh;
  width: 40vh;
  padding-top: 20px;
  overflow: hidden;
  background-size: auto;
}

.item1 {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
}

.item2 {
  grid-row: 2 / 3;
  grid-column: 2 / 2;
  height: 80px;
  width: 90px;
  margin-right: 15px;
  margin-bottom: 20px;
}
.item3 {
  grid-row: 3 / 4;
  grid-column: 2 / 3;
  height: 80px;
  width: 95px;
  margin-bottom: 25px;
}

.item4 {
  grid-row: 4 / 5;
  grid-column: 3 / 2;
  height: 80px;
  width: 90px;
  margin-right: 20px;
}

.item5 {
  grid-row: 5 / 6;
  grid-column: 3 / 3;
  height: 80px;
  width: 90px;
}

.item6 {
  grid-row: 6 / 7;
  grid-column: 4 / 2;
  height: 80px;
  width: 90px;
  margin-top: 25px;
  margin-right: 20px;
}

.item7 {
  grid-row: 7 / 8;
  grid-column: 4 / 3;
  height: 80px;
  width: 90px;
}

.item8 {
  grid-row: 8 / 9;
  grid-column: 5 / 2;
  height: 80px;
  width: 90px;
  margin-top: 25px;
  margin-right: 20px;
}

.item9 {
  grid-row: 9 / 10;
  grid-column: 5 / 3;
  height: 80px;
  width: 90px;
}

.item10 {
  grid-row: 10 / 11;
  grid-column: 6 / 2;
  height: 80px;
  width: 90px;
  margin-top: 15px;
  margin-right: 15px;
}

.item11 {
  grid-row: 11 / 12;
  grid-column: 6 / 3;
  height: 80px;
  width: 90px;
  margin-right: 5px;
}

.item12 {
  grid-row: 12 / 13;
  grid-column: 7 / 2;
  height: 80px;
  width: 90px;
  margin-top: 20px;
}

.item13 {
  grid-row: 13 / 14;
  grid-column: 7 / 3;
  height: 80px;
  width: 90px;
}
